/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Can't Happen Here","date":"01 Jul 2004","slug":"/2004-07-01-cant-happen-here/","tags":"Y2004"},"html":"<p>Spiegel Online: <a href=\"http://www.spiegel.de/panorama/0,1518,306732,00.html\" title=\"Beziehungsstress: Tourist lässt Ehefrau an Tankstelle stehen - Panorama - SPIEGEL ONLINE\">German tourist leaves wife at gas station</a> \"Returning with their camper from a vacation on the Arctic Circle, a German tourist from Stuttgart left his wife at a Danish gas station after a heavy argument and never returned. The woman was allowed to sleep on a mattress at a police station, and was sent home by train the next day.\" That could never happen to us, since my wife always drives. &lt;rimShot /&gt;</p>"},"next":{"fields":{"title":"It's Over","date":"30 Jun 2004","slug":"/2004-06-30-its-over/"}},"previous":{"fields":{"title":"Greece is the better Germany","date":"01 Jul 2004","slug":"/2004-07-01-greece-is-the-better-germany/"}}},{"node":{"fields":{"title":"It's Over","date":"30 Jun 2004","slug":"/2004-06-30-its-over/","tags":"Y2004"},"html":"<p>We can forget any suspense at the Euro 2004, since Anna has <a href=\"http://www.jinglelady.us/blog/archives/000617.html\" title=\"Adventures of an American Girl in Germany: My picks for the final Euro2004 games\">published her favorites for the final games</a>, and her favorites have without fail all lost. The prospect of Holland as champion is horrifying for German fans (according to Duden, <em>Schadenfreude</em> is defined as \"disappointed Dutch fans; see <a href=\"http://www.papascott.de/archives/2002/06/12/world-cup-how-german-soccer-fans-feel/\">World Cup 2002</a>\"), but one we have to accept.</p>"},"next":{"fields":{"title":"Pulling out of here to win","date":"29 Jun 2004","slug":"/2004-06-29-pulling-out-of-here-to-win/"}},"previous":{"fields":{"title":"Can't Happen Here","date":"01 Jul 2004","slug":"/2004-07-01-cant-happen-here/"}}},{"node":{"fields":{"title":"Pulling out of here to win","date":"29 Jun 2004","slug":"/2004-06-29-pulling-out-of-here-to-win/","tags":"Y2004"},"html":"<p>It looks like we're set. Christopher begins kindergarten on Aug 5. As we hoped, he was admitted into the speech therapy group in Winsen. We were at their Sommerfest last weekend, and they had already had him registered and assigned to a room (he'll be in the 'penguin' group) and teachers. Our first parents meeting is this Thursday, when they'll explain what he needs to bring (from the looks of it, each kid has nearly a complete wardrobe of indoor and outdoor gear on hand).</p>\n<p>The next surprise was at work. I told my boss that we got the kindergarten of our choice, and that starting in August I'd have to be home by 3 instead of 5. He said no problem, no need to go part time, I can make up the hours from home. That's fine as long as we have short time, that's only a couple of hours a week. When we go back to full hours, I'm not sure how things will work. </p>\n<p>My decisions would be easier if some choices were eliminated for me. If it were impossible for me to continue work, either because of kindergarten or bankruptcy, then I could always implement Plan B. But now that I <em>can</em> continue to work, I have to decide for myself whether I really <em>want</em> to. That will be hard.</p>\n<p>(It's also hard to write about. We've known all this since last weekend, and it took me over a week to decide how much, and how little, to write about it.)</p>"},"next":{"fields":{"title":"I Have No Goat","date":"27 Jun 2004","slug":"/2004-06-27-i-have-no-goat/"}},"previous":{"fields":{"title":"It's Over","date":"30 Jun 2004","slug":"/2004-06-30-its-over/"}}}],"pathPrefix":"","first":false,"last":false,"index":460,"pageCount":1101}}

/***/ })

